import {useState} from 'react';
import {evaluate} from 'mathjs';
import "../styles/calculator.css";

function Calculator() {
    const [calc, setCalc] = useState("");
	const [result, setResult] = useState("");

	const ops = ["/", "*", "+", "-", "."];

	const updateCalc = value => {
		if (
			(ops.includes(value) && calc === '') ||
			(ops.includes(value) && ops.includes(calc.slice(-1)))
		) {
			return;
		}

		setCalc(calc + value);

		if (!ops.includes(value)) {
			setResult(evaluate(calc + value).toString())
		}
	}

	const createDigits = () => {
		const digits = [];

		for (let i = 1; i < 10; i++) {
      digits.push(
        <button onClick={() => updateCalc(i.toString())} key={i}>{i}</button>
      );
		};

		return digits;
	}

	const calculate = () => {
		setCalc(evaluate(calc).toString());
	}

	const deleteLast = () => {
		if (calc === '') {
			return;
		}

		const value = calc.slice(0, -1);

		setCalc(value);
	}

	const clear = () => {
		const value = "";
		setCalc(value);
		setResult(value);
	}

	const getNinePercent = () => {
		setCalc(evaluate(calc * 0.09).toString());
    setResult(evaluate(calc * 0.09).toString());
	}

	const getElevenPercent = () => {
		setCalc(evaluate(calc * 0.11).toString());
    setResult(evaluate(calc * 0.11).toString());
	}

	const getSquared = () => {
		setCalc(evaluate(calc ** 2).toString());
		setResult(evaluate(calc ** 2).toString());
	}

	const getCubed = () => {
		setCalc(evaluate(calc ** 3).toString());
		setResult(evaluate(calc ** 3).toString());
	}

	const getSquareroot = () => {
		setCalc(Math.sqrt(calc).toString());
		setResult(Math.sqrt(calc).toString());
	}

	const getCubedroot = () => {
		setCalc(Math.cbrt(calc).toString());
		setResult(Math.sqrt(calc).toString());
	}

	return (
		<div className="main-container">
			<div className="calculator">
				<div className="title">
					<h1>Finance Calculator</h1>
				</div>
				
				<div className="display">
					{result ? <span>({result})</span> : '' } { calc || "0" }
				</div>

				<div className="operators">
					<button onClick={() => updateCalc('/')}>/</button>
					<button onClick={() => updateCalc('*')}>*</button>
					<button onClick={() => updateCalc('+')}>+</button>
					<button onClick={() => updateCalc('-')}>-</button>

					<button onClick={clear}>CLR</button>
				</div>

				<div className="operators">
					<button onClick={getSquared}>x<sup>2</sup></button>
					<button onClick={getCubed}>x<sup>3</sup></button>
					<button onClick={getSquareroot}><sup>2</sup>&#x221A;x</button>
					<button onClick={getCubedroot}><sup>3</sup>&#x221A;x</button>
					<button onClick={() => updateCalc('3.14')}>&#x3C0;</button>
				</div>

				<div className="digits">
					{ createDigits() }
          			<button onClick={() => updateCalc('0')}>0</button>
					<button onClick={() => updateCalc('.')}>.</button>
					<button onClick={calculate}>=</button>
					<button onClick={deleteLast}>DEL</button>
					<button onClick={getNinePercent}>9%</button>
					<button onClick={getElevenPercent}>11%</button>
				</div>
			</div>
		</div>
	);
}

export default Calculator;