import React from 'react';
import {
    MainContainer,
    InputStyle,
    ButtonStyle,
    TitleContainer,
    FormContainer,
    Title,
    Title3,
    CalculatorStyle,
    TableContainer,
    TableRow,
    TableHeader,
    TableData,
    LabelStyle
} from "../styles/loanCalc.style";


function LoanCalculator() {
    const calculate = () => {
        let amount = document.querySelector("#amount").value;
        let rate = document.querySelector("#rate").value;
        let years = document.querySelector("#years").value;

        let principal = parseFloat(amount);
        let calculatedInterest = parseFloat(rate) / 100 / 12;
        let calculatedPayment = parseFloat(years) * 12;

        let x = Math.pow(1 + calculatedInterest, calculatedPayment);
        let monthly = (principal * x * calculatedInterest) / (x - 1);
        let totalInterest = ((monthly * calculatedPayment) - principal);
        let totalPayment = monthly * calculatedPayment;

        if (isFinite(monthly)) {
            document.querySelector("#loan-amount").innerHTML = `$${principal.toString()}`;
            document.querySelector("#ann-apr").innerHTML = `${rate}%`;
            document.querySelector("#term").innerHTML = `${years} yrs`;
            document.querySelector("#monthly").innerHTML = `$${monthly.toFixed(2)}`;
            document.querySelector("#total-interest").innerHTML = `$${totalInterest.toFixed(2)}`;
            document.querySelector("#total").innerHTML = `$${totalPayment.toFixed(2)}`;
        } else {
            alert("Check Numbers Again");
            return;
        }
    }

    return (
        <MainContainer>
            <TitleContainer>
                <Title>Fast As Fuck Boy Loan Calculator</Title>
            </TitleContainer>
            <CalculatorStyle>
                <TableContainer>
                    <Title3>Loan Break Down</Title3>
                    <TableRow>
                        <TableHeader>Loan Amount</TableHeader>
                        <TableData id="loan-amount"></TableData>
                    </TableRow>
                    <TableRow>
                        <TableHeader>AP Rate</TableHeader>
                        <TableData id="ann-apr"></TableData>
                    </TableRow>
                    <TableRow>
                        <TableHeader>Term in Years</TableHeader>
                        <TableData id="term"></TableData>
                    </TableRow>
                    <TableRow>
                        <TableHeader>Monthly Payments (Compounded Interest)</TableHeader>
                        <TableData id="monthly"></TableData>
                    </TableRow>
                    <TableRow>
                        <TableHeader>Total Interest</TableHeader>
                        <TableData id="total-interest"></TableData>
                    </TableRow>
                    <TableRow>
                        <TableHeader>Total After Complete</TableHeader>
                        <TableData id="total"></TableData>
                    </TableRow>
                </TableContainer>
                <FormContainer>
                    <LabelStyle>Loan Amount</LabelStyle>
                    <InputStyle id="amount" type="number" placeholder="$25,000.86" />
                    <LabelStyle>Annual Percentage Rate (APR)</LabelStyle>
                    <InputStyle id="rate" type="number" placeholder="29.89%" />
                    <LabelStyle>Months In Term<span>?</span></LabelStyle>
                    <InputStyle id="years" type="number" placeholder="13 years" />
                    <ButtonStyle onClick={calculate}>Calculate</ButtonStyle>
                </FormContainer>
            </CalculatorStyle>
        </MainContainer>
    );
}

export default LoanCalculator;