import React from 'react';
import {
    MainContainer,
    ButtonStyle,
    DisplayStyle,
    TitleContainer,
    Title,
    BodyContainer
} from "../styles/counter.style";


function Counter() {
    function updateCount(e) {
        let value = e.target.value;

        if (value === "add") {
            document.getElementById("counter-display").innerHTML++;
        } else {
            document.getElementById("counter-display").innerHTML--;
        }
    }

    return (
        <MainContainer>
            <TitleContainer>
                <Title>Let's Get To Counting :D</Title>
            </TitleContainer>
            <BodyContainer>
                <ButtonStyle value="sub" onClick={updateCount}>-</ButtonStyle>
                <DisplayStyle id="counter-display">0</DisplayStyle>
                <ButtonStyle value="add" onClick={updateCount}>+</ButtonStyle>
            </BodyContainer>
        </MainContainer>
    );
}

export default Counter;