import React, {Component} from 'react';
import {
    ClockContainer,
    TimeClock
} from "../styles/clock.style";


class ClockDisplay extends Component {
    constructor() {
        super();

        const dayNames = ["Sunday", "Monday", "Tuesday", "Wednesday", "Thursday", "Friday", "Saturday"];
        const months = ["January", "February", "March", "April", "May", "June", "July", "August", "September", "October", "November", "December"];

        let dateObj = new Date();
        let hour = dateObj.getHours();
        let minute = dateObj.getMinutes();
        let second = dateObj.getSeconds();
        let month = dateObj.getMonth();
        let day = dateObj.getDay();
        let dateDay = dateObj.getDate();
        let year = dateObj.getFullYear();
        let session = "AM";

        let dayNumber = parseInt(day);
        let monthNumber = parseInt(month);

        if (hour > 12) {
            hour = hour - 12;
            session = "PM";
        }

        if (monthNumber > 0 && dayNumber > 0) {
            day = dayNames[dayNumber-1];
            month = months[monthNumber];
        } else if (monthNumber > 0 && dayNumber === 0) {
            day = dayNames[dayNumber];
            month = months[monthNumber]
        }


        let dateString = day + " " + month + " " + dateDay + ", " + year;
        let timeString = hour + ":" + minute + ":" + second + " " + session;

        this.state = {
            currentDate: dateString,
            currentTime: timeString
        }
    }

    render() {
        return (
            <ClockContainer>
                <TimeClock id="clock-display">
                    {this.state.currentDate}<br />{this.state.currentTime}
                </TimeClock>
            </ClockContainer>
        );
    }
}

export default ClockDisplay;