import React from 'react';
import {
    MainContainer,
    Buttons,
    ButtonStyle,
    TitleContainer,
    Title,
    LinkStyle,
    Details
} from "../styles/home.style";
import {useNavigate} from "react-router-dom";
import ClockDisplay from './DateAndTime';

function Home() {
    let navigate = useNavigate();

    const goCalc = () => {
        let path="/calc";
        navigate(path);
    }

    const goCount = () => {
        let path="/count";
        navigate(path);
    }

    const goLoan = () => {
        let path="/finCalc";
        navigate(path);
    }
    
    return (
        <MainContainer>
            <TitleContainer>
                <Title>Mekasu's Projects ^_^</Title>
                <Details>
                    Here you can find all of the projects I have completed.
                    Please, <LinkStyle href="https://github.com/shellbyy0124/meks_projects.github.io/issues" target="_blank" rel="noreferrer">click here</LinkStyle> to get help.
                </Details>
            </TitleContainer>
            <ClockDisplay />
            <Buttons>
                <ButtonStyle onClick={goCalc}>Calculator</ButtonStyle>
                <ButtonStyle onClick={goCount}>Counter</ButtonStyle>
                <ButtonStyle onClick={goLoan}>Loan Calculator</ButtonStyle>
            </Buttons>
        </MainContainer>
    );
}

export default Home;